.footer {
  flex-shrink: 0;
  background: var(--gradient-2);
  color: var(--color-text-light);
  padding: 24px 0;
  &__nav-link {
    
  }
  &__container {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    align-items: center;
  }
  &__right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__link {
    transition: color $trs1;
    font-size: 0.75rem;
    &[data-touch], &[data-hover] {
      color: var(--color-accent-2);
    }
  }
  &__logo {
    margin-bottom: 4px;
  }
  &__socials {
    margin-bottom: 8px;
  }
  &__copyright {
    display: block;
    font-size: 0.75rem;
  }


  @include media-breakpoint-down(lg) {
    padding: 40px 0;
    &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__left {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 30px;
    }
    &__nav {
      margin-bottom: 30px;
    }
    &__right {
      align-items: center;
    }
    &__socials {
      margin-bottom: 10px;
    }
  }
}